body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

header {
  background-color: #282c34;
  color: white;
  padding: 20px 0;
  text-align: center;
  font-size: 0.8em;
  font-weight: bold;
}

header ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: space-around; /* Cambiado a space-around para dar espacio a los lados */
  align-items: center; /* Agregado para centrar verticalmente */
}

header ul li {
  cursor: pointer;
}

nav {
  background-color: #ddd;
  padding: 10px 0;
  margin-bottom: 15px;
  width: 100%; /* Cambiado a 100% para ocupar todo el ancho */
  display: flex;
  justify-content: center;
}

nav ul {
  list-style-type: none;
  padding: 0;
  text-align: center;
}

nav ul li {
  display: block;
  transition: background-color 1s ease, transform 1s ease;
}

nav ul li:hover {
  background-color: #f4f4f4;
  animation: despliegue 1s forwards;
}

@keyframes despliegue {
  0% { transform: scale(1); }
  100% { transform: scale(1.1); }
}

nav ul li:active {
  animation: contraccion 1s forwards;
}

@keyframes contraccion {
  0% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

nav ul li a {
  text-decoration: none;
  color: #090f16;
  font-size: 0.9em;
}

nav ul li {
  padding: 10px;
  margin: 5px 0;
}

footer {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
  font-size: 0.8em;
}

nav ul li {
  cursor: pointer;
}

Routes {
  cursor: default;
}

